/* HOME */
nav{
  z-index: 10;
  font-family: 'Questrial';
}
.video-bg{
  height: 100vh;
}
.text-proposito{
  width: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);

}
.text-proposito h1{
  text-transform: uppercase;
  font-size: 1.8rem;
  color:#7DC7C5;
  font-family: 'Questrial';
  font-weight: bolder!important;
  line-height: 20px;

}
.text-proposito .megafono{
  width: 3rem;
  height: auto;
  margin-top: -2rem
}

.text-proposito .h1-proposito{
  margin-left: 5rem;
  margin-bottom: 1rem
}
.text-proposito p{
  line-height: 20px;
  font-family: 'Questrial', sans-serif;
}
.arrow-orange{
    width: 1.2rem;
    height: auto;
    margin-left: 3px;
    margin-right: 3px;
}
.btn-orange{
  background-color: #EF8B44;
  padding: 14px 12px;
  border-radius: 25px;
  box-shadow: 1px 1px 1px 1px #CE6A24;
  text-transform: uppercase;
}

.nosotros-home{
  font-family: 'Questrial', sans-serif;
  text-align: center
}
.nosotros-home h2{
  text-transform: uppercase;
  color: #7DC7C5;
  font-weight: 800;
  font-size: 1.8rem
}
.nosotros-home p{
  line-height: 20px;
}
.nosotros-home .btn-orange{
  position: relative;
    top: 2rem;
}
.box-estrategias{
  margin-top: 2rem;
  margin-bottom: 1rem;
  width: 100%;
  display: flex;
}
 .box-estrategias .card{
  width: 34%;
  border: 0px
}
 .box-estrategias .card-img-top{
  width: 50%;
  margin: auto
}
 .box-estrategias .card-body{
  padding: 0px;
  color: #7DC7C5
}

.group-nosotros{
  position: relative;
  left: -33%;
  bottom: -3rem;
  width: 100%;
}
.cohete{
  position: relative;
    bottom: -4rem;
    z-index: 1;
    display: block;
    left: 50%;
    transform: translate(-50%)
}
.img-estrategias{
  width: 20%;
  height: auto;
}

.equipo-home{
  background-color: #7DC7C5;
  z-index: 5;
  position: relative;
  text-align: center;
}
.equipo-home h2{
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
  font-family: 'Questrial';
  padding-top: 2rem;
  margin-bottom: 1rem;
  font-size: 1.8rem
}
.equipo-home .card{
background-color: rgba(0,0,0,0);
border:none
 }
 .equipo-home .card img{
   width: 70%;
   margin: auto
 }
.equipo-home .card-body{
  background-color: #ef8b44de;
  /* opacity: .9; */
  width: 70%;
  margin: auto;
  position: relative;
  top: -4rem;
  height: 70px;
  padding: .8rem;

  font-weight: 400;
  text-align: left
}
.equipo-home .card-body h6{
  color: #fff;
  font-family: "Questrial";
  font-size: 1rem
}
.equipo-home .controls-top{
  top: 9rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  z-index: 5
}
.equipo-home .controls-top a{
  color: #fff
}
/* HOME */


